csui.define('csui/controls/form/fields/alpaca/alpcsuipickerfield.mixin',['csui/lib/underscore', 'csui/lib/jquery',
  'csui/lib/backbone', 'csui/lib/marionette'
], function (_, $, Backbone, Marionette) {
  
      var AlpCsuiPickerFieldMixin = {
        mixin: function (prototype) {
    
          return _.extend(prototype, {
  
            makeAlpCsuiPickerField: function () {
            },
  
            getFieldType: function () {
              return this.options.alpacaFieldType;
            },
        
            postRender: function (callback) {
              this.base(callback);
              this.showField();
              this.field.parent().addClass("csui-field-" + this.getFieldType());
              this.field.parent().addClass(this.options.alpacaFieldClasses||'csui-field-otcs_csuipicker');

            },
        
            showField: function () {
        
              var id = this.id;
        
              // add an id to the <label> element like alpaca23Label
              var id4Label,
                  id4Description = this.options ? this.options.descriptionId : '',
                  labelElement   = $(this.field[0]).find('label');
        
              if (labelElement && labelElement.length == 1) {
                id4Label = labelElement.attr('for') + "Label";
                labelElement.attr('id', id4Label);
              }
        
              // our field
              this.fieldView = new this.options.PickerFieldView({
                context: this.connector.config.context,
                formView: this.connector.config.formView,
                model: new Backbone.Model({
                  data: this.data,
                  options: this.options,
                  schema: this.schema,
                  id: id
                }),
                id: _.uniqueId(id), // wrapper <div>
                alpacaField: this,
                labelId: id4Label,
                descriptionId: id4Description,
                value: this.data,
                readonly: true,
                dataId: this.name,
                path: this.path,
                alpaca: {
                  data: this.data,
                  options: this.options,
                  schema: this.schema
                }
              });
        
              // replace alpaca control with empty div having same classes as control
              var $field = $('<div>').addClass('alpaca-control');
              this.getControlEl().replaceWith($field);
        
              // show our field in div
              this.region = new Marionette.Region({el: $field});
              this.region.show(this.fieldView);
        
              return;
            },
        
            setValueAndValidate: function (value, validate) {
              this.setValue(value);
              var bIsValid = true;
              if (validate) {
                bIsValid = this.validate();
                this.refreshValidationState(false);
              }
              return bIsValid;
            },
        
            focus: function () {
              this.fieldView.$el.trigger('focus');
            },
        
            destroy: function () {
              this.base();
              if (this.region) {
                this.region.destroy();
              }
            }
  
          });
        }
      };
    
      return AlpCsuiPickerFieldMixin;
  });

